.facilities-page {
  min-height: 100vh;
  background: #eaf5ff;
  overflow-x: hidden;
}

.facilities-one-section {
  padding: 34px 20px 0;
}

.facilities-shell {
  width: min(1200px, 100%);
  margin: 0 auto;
}

.facilities-local-header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 24px;
  margin-bottom: 46px;
}

.facilities-local-logo {
  color: #0f172a;
  font-size: 1.9rem;
  letter-spacing: 0.1em;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1;
}

.facilities-local-logo .brand-logo-img {
  height: 42px;
  width: auto;
  display: block;
}

.facilities-local-logo span {
  font-weight: 400;
  color: #334155;
  opacity: 1;
}

.facilities-local-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 34px;
}

.facilities-local-nav a {
  color: #0f172a;
  font-size: 1.05rem;
  text-decoration: none;
}

.facilities-local-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.facilities-local-btn {
  height: 46px;
  border-radius: 999px;
  border: 1px solid transparent;
  padding: 0 22px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #0f766e;
  font-size: 0.98rem;
  font-weight: 500;
  text-decoration: none;
}

.facilities-local-btn-solid {
  background: linear-gradient(135deg, #38bdf8, #0ea5e9);
  color: #ffffff;
}

.facilities-local-btn-outline {
  background: rgba(255, 255, 255, 0.72);
  border-color: rgba(14, 165, 233, 0.45);
  color: #0369a1;
}

.facilities-copy {
  text-align: center;
}

.facilities-copy h1 {
  margin: 14px 0 0;
  color: #0369a1;
  font-size: clamp(1.85rem, 3.8vw, 3.6rem);
  line-height: 1.12;
  font-weight: 500;
}

.facilities-cards {
  margin-top: 52px;
  display: grid;
  gap: 20px;
  padding-bottom: 14px;
}

.facilities-security-full {
  width: 100%;
  margin-top: 24px;
  display: grid;
  grid-template-columns: 0.9fr 1.1fr;
  min-height: 400px;
  background: #f8fbff;
  border-radius: 26px;
  overflow: hidden;
  box-shadow: 0 18px 36px rgba(2, 132, 199, 0.1);
}

.facilities-security-media {
  height: 100%;
}

.facilities-security-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.facilities-security-content {
  padding: 44px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.facilities-security-content h2 {
  margin: 0;
  color: #0c4a6e;
  font-size: clamp(1.8rem, 3vw, 3.2rem);
  line-height: 1.15;
  font-weight: 500;
}

.facilities-security-content p {
  margin: 18px 0 0;
  color: #1e3a5f;
  font-size: clamp(1.02rem, 1.3vw, 1.12rem);
  line-height: 1.55;
}

.facilities-bottom-unified {
  margin-top: 24px;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
}

.facilities-bottom-unified .hostel-cartoon-section {
  margin: 0;
}

.facilities-feature-card {
  display: grid;
  grid-template-columns: 0.9fr 1.1fr;
  min-height: 400px;
  background: #ffffff;
  border-radius: 32px;
  overflow: hidden;
  box-shadow: 0 18px 40px rgba(2, 132, 199, 0.12);
  transition: transform 0.28s ease, box-shadow 0.28s ease;
}

.facilities-feature-card:nth-child(even) {
  grid-template-columns: 1.1fr 0.9fr;
}

.facilities-feature-card:nth-child(even) .facilities-feature-media {
  order: 2;
}

.facilities-feature-card:nth-child(even) .facilities-feature-content {
  order: 1;
}

.facilities-feature-media {
  height: 100%;
  overflow: hidden;
}

.facilities-feature-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.45s ease;
}

.facilities-feature-content {
  padding: 44px 46px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.facilities-feature-content h2 {
  margin: 0;
  color: #0c4a6e;
  font-size: clamp(1.8rem, 3vw, 3.2rem);
  line-height: 1.15;
  font-weight: 500;
}

.facilities-feature-content p {
  margin: 18px 0 0;
  max-width: 52ch;
  color: #0f172a;
  font-size: clamp(1.02rem, 1.3vw, 1.12rem);
  line-height: 1.55;
}

.facilities-feature-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 28px 52px rgba(2, 132, 199, 0.18);
}

.facilities-feature-card:hover .facilities-feature-media img {
  transform: scale(1.03);
}

@media (max-width: 1024px) {
  .facilities-cards {
    gap: 16px;
  }

  .facilities-feature-card {
    grid-template-columns: 1fr;
    border-radius: 22px;
  }

  .facilities-feature-card:nth-child(even) .facilities-feature-media,
  .facilities-feature-card:nth-child(even) .facilities-feature-content {
    order: initial;
  }

  .facilities-feature-media {
    min-height: 270px;
  }

  .facilities-feature-content {
    padding: 28px 24px 24px;
  }
}

@media (max-width: 640px) {
  .facilities-one-section {
    padding: 34px 14px 50px;
  }

  .facilities-local-header {
    grid-template-columns: 1fr;
    gap: 16px;
    margin-bottom: 24px;
    text-align: center;
  }

  .facilities-local-logo {
    font-size: 1.5rem;
  }

  .facilities-local-logo .brand-logo-img {
    height: 34px;
  }

  .facilities-local-nav {
    gap: 18px;
    flex-wrap: wrap;
  }

  .facilities-local-actions {
    justify-content: center;
    flex-wrap: wrap;
  }

  .facilities-copy h1 {
    margin-top: 10px;
  }

  .facilities-cards {
    margin-top: 28px;
    gap: 14px;
  }

  .facilities-feature-card {
    grid-template-columns: 1fr;
    border-radius: 18px;
  }

  .facilities-feature-card:nth-child(even) .facilities-feature-media,
  .facilities-feature-card:nth-child(even) .facilities-feature-content {
    order: initial;
  }

  .facilities-feature-media {
    min-height: 220px;
  }

  .facilities-feature-content {
    padding: 20px 16px 18px;
  }

  .facilities-feature-content p {
    margin-top: 10px;
  }

  .facilities-security-full {
    grid-template-columns: 1fr;
    border-radius: 18px;
  }

  .facilities-security-media {
    min-height: 220px;
  }

  .facilities-security-content {
    padding: 20px 16px 18px;
  }
}

.director-message-section {
  margin: 18px 0 28px;
}

.director-message-heading {
  margin: 0 0 14px;
  font-size: clamp(1.25rem, 2.2vw, 1.85rem);
  letter-spacing: 0.03em;
  color: #0f172a;
}

.director-message-grid {
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
  gap: 18px;
  align-items: stretch;
}

.director-message-media,
.director-message-content {
  min-height: 100%;
}

.director-message-media {
  height: 100%;
}

.director-message-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 18px;
  display: block;
}

.director-message-content {
  background: #f8fcff;
  border: 1px solid rgba(14, 165, 233, 0.18);
  border-radius: 18px;
  padding: 20px;
  color: #334155;
  line-height: 1.75;
}

.director-message-content p {
  margin: 0 0 12px;
}

.director-message-content .director-signoff {
  margin-bottom: 0;
  color: #0f172a;
  font-weight: 500;
}

@media (max-width: 900px) {
  .director-message-grid {
    grid-template-columns: 1fr;
  }

  .director-message-media img {
    height: auto;
  }
}

.vision-mission-section {
  margin: 0 0 28px;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  border-radius: 0;
  padding: clamp(18px, 2.3vw, 34px);
  background: #0d3f66;
  box-shadow: 0 18px 42px rgba(7, 43, 82, 0.28);
}

.vision-mission-grid {
  display: grid;
  grid-template-columns: minmax(0, 1.05fr) minmax(0, 0.95fr);
  gap: clamp(16px, 2vw, 28px);
  align-items: center;
  max-width: 1220px;
  margin: 0 auto;
}

.vision-mission-copy h2 {
  margin: 0 0 10px;
  font-size: clamp(1.45rem, 2vw, 2rem);
  color: #e0f2fe;
}

.vision-mission-copy h2 + ul {
  margin-bottom: 20px;
}

.vision-mission-copy ul {
  margin: 0;
  padding-left: 20px;
  color: #f8fbff;
  line-height: 1.7;
}

.vision-mission-copy li + li {
  margin-top: 10px;
}

.vision-mission-media {
  position: relative;
  min-height: 330px;
}

.vision-mission-main-image {
  width: 78%;
  max-width: 420px;
  height: 330px;
  object-fit: cover;
  border-radius: 18px;
  box-shadow: 0 16px 34px rgba(0, 0, 0, 0.3);
}

.vision-mission-overlay-image {
  position: absolute;
  right: 0;
  bottom: 6%;
  width: 62%;
  max-width: 300px;
  height: 220px;
  object-fit: cover;
  border-radius: 16px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.28);
  border: 3px solid rgba(224, 242, 254, 0.45);
}

@media (max-width: 900px) {
  .vision-mission-grid {
    grid-template-columns: 1fr;
  }

  .vision-mission-media {
    min-height: 300px;
  }

  .vision-mission-main-image {
    width: 84%;
    height: 280px;
  }

  .vision-mission-overlay-image {
    width: 58%;
    height: 170px;
  }
}

.key-amenities-section {
  margin: 0 0 28px;
}

.key-amenities-shell {
  background: linear-gradient(165deg, #d8eaf6 0%, #c4deef 100%);
  border-radius: 20px;
  border: 1px solid rgba(13, 63, 102, 0.14);
  padding: clamp(18px, 2.4vw, 30px);
  box-shadow: 0 14px 34px rgba(13, 63, 102, 0.18);
}

.key-amenities-shell h2 {
  margin: 0;
  font-size: clamp(1.55rem, 2.2vw, 2.2rem);
  letter-spacing: 0.05em;
  color: #0d3f66;
  text-shadow: 0 6px 16px rgba(13, 63, 102, 0.2);
}

.key-amenities-icon-strip {
  margin-top: 16px;
  display: grid;
  grid-template-columns: repeat(8, minmax(0, 1fr));
  gap: 10px;
  padding: 14px;
  background: rgba(255, 255, 255, 0.74);
  border: 1px solid rgba(13, 63, 102, 0.15);
  border-radius: 14px;
}

.key-amenities-icon {
  min-height: 64px;
  border-radius: 12px;
  background: #ffffff;
  border: 1px solid rgba(13, 63, 102, 0.12);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #0d3f66;
  box-shadow: 0 8px 16px rgba(13, 63, 102, 0.12);
}

.key-amenities-shell h3 {
  margin: 18px 0 10px;
  text-align: center;
  color: #c1121f;
  font-size: clamp(1.2rem, 1.8vw, 1.55rem);
  text-decoration: underline;
}

.key-amenities-list {
  margin: 0;
  padding-left: 28px;
  color: #0f172a;
  font-size: clamp(1rem, 1.25vw, 1.14rem);
  line-height: 1.6;
}

.key-amenities-list li + li {
  margin-top: 9px;
}

@media (max-width: 1100px) {
  .key-amenities-icon-strip {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

@media (max-width: 640px) {
  .key-amenities-shell {
    border-radius: 14px;
    padding: 14px;
  }

  .key-amenities-icon-strip {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    padding: 10px;
  }

  .key-amenities-icon {
    min-height: 54px;
  }

  .key-amenities-list {
    padding-left: 22px;
  }
}
